@import "../mixins/mixins";
@import "../root/root";

$telegram-bg-color: #337ab7;
$black-bg-color: #000;
$text-color: #fff;

.button {
  display: block;
  position: relative;
  color: $text-color;
  border: none;
  cursor: pointer;
  width: 100%;
  height: 100%;
  transition: all 0.2s ease-in-out;
  overflow: hidden;
  border-radius: 5px;
}

.button_color_telegram {
  background-color: #337ab7;
}

.button_color_github {
  background-color: #000;
}

.button_color_default {
  background-color: #000;
}
.button_color_hh {
  background-color: #e1011c;
}
.button_color_habr {
  background-color: #65a2b9;
}
.button_color_test{
  background-color: none;
  border: 2px solid #000;
  color: #000 !important;
}
.button_color_pdf {
  background-color: #fff;
  color: black;
}

.contact__button_inactive {
  transform: scale(0.5);
}
.button__text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 200%);
  transition: all 0.2s linear;
  text-transform: uppercase;
}


.button__icon {
  position: absolute;
  width: 25px;
  height: 25px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.2s linear;
  @include pos-center();
}

.button_transform_horizontal .button__text {
  transform: translate(-800%, -50%);
}


@include iHover {
  .button_transform_vertical:hover .button__text {
    transform: translate(-50%, -50%);
  }
  .button_transform_vertical:hover .button__icon {
    transform: translate(-50%, -300%);
  }
  .contact__button:hover .button__text {
    transform: translate(-50%, -50%);
  }
  
  .contact__button:hover .button__icon {
    transform: translate(-50%, -200%);
  }
  
  .button_transform_horizontal:hover .button__icon {
    transform: translate(230%, -50%);
  }
  
  .button_transform_horizontal:hover .button__text {
    transform: translate(-50%, -50%);
  }

}
@include laptop {
  .button_transform_vertical .button__text {
    transform: translate(-50%, -50%);
  }
  .button_transform_vertical .button__icon {
    transform: translate(-50%, -300%);
  }
  .contact__button .button__text {
    transform: translate(-50%, -50%);
  }
  
  .contact__button .button__icon {
    transform: translate(-50%, -200%);
  }
  
  .button_transform_horizontal .button__icon {
    transform: translate(230%, -50%);
  }
  
  .button_transform_horizontal .button__text {
    transform: translate(-50%, -50%);
  }
}



