@use "../mixins/mixins";


.social-media-list {
  margin: 0;
  // max-width: 80px;
  line-height: 1.42857143;
  color: #333;
  box-sizing: border-box;
  font-size: 22px;
  text-align: center;
  width: 100%;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include tablet {
    max-width: 85%;
  }
  & li {
    font-size: 22px;
    box-sizing: border-box;
    position: relative;
    z-index: 999;
    display: inline-block;
    height: 60px;
    width: 60px;
    line-height: 60px;
    border-radius: 50%;
    color: #fff;
    background-color: rgb(27, 27, 27);
    cursor: pointer;
    transition: all 0.2s ease-in-out;


    &:hover {
      background-color: #fff;

      &::after {
        opacity: 1;
        transform: scale(1.12);
        transition-timing-function: cubic-bezier(0.37, 0.74, 0.15, 1.65);
      }

      & a {
        color: #000;
      }
    }

    & a {
      font-size: 22px;
      line-height: 60px;
      box-sizing: border-box;
      background-color: transparent;
      text-decoration: none;
      color: #fff;
  
    }
  }

  
}
